@import url(https://fonts.googleapis.com/css?family=Lato:400,700,400italic,700italic&subset=latin);
body {
  padding: 0;
  margin: 0;
}
.org-tree {
  float: left;
  width: 25%;
  height: 100%;
}



#logout[data-v-5515c325], #userinfo[data-v-5515c325] {
  height: 60px;
  line-height: 60px;
  float: right;
}

.content[data-v-86d2e388] {
  padding: 12px;
  text-align: center;
}

#id-msg-pagination[data-v-1e1f2b48] {
  margin-top: 20px;
}
#id-add-msg-btn[data-v-1e1f2b48] {
  margin-top: 12px;
}
#title[data-v-1e1f2b48] {
  text-align: left;
  text-indent: 14px;
}
#title > a[data-v-1e1f2b48] {
  color: #2e2e2e;
}
.info[data-v-1e1f2b48] {
  padding-left: 20px;
  margin: 24px 12px;
}
.info > h3.info-title[data-v-1e1f2b48] {
  text-align: left;
  font-size: 14px;
  color: grey;
}
.info .info-content[data-v-1e1f2b48] {
  color: #409eff;
  font-size: 35px;
  font-weight: 100;
}
.info .info-footer[data-v-1e1f2b48] {
  font-size: 12px;
  display: block;
  text-align: left;
  color: rgba(1, 1, 1, .3);
  padding-top: 12px;
}
.info-title[data-v-1e1f2b48]::after {
  display: inline-block;
  height: 2px;
  width: 150px;
}
.info-parent[data-v-1e1f2b48] {
  margin: 24px 0;
}

/*.info-parent:hover {*/

/*box-shadow: 0 1px 1px 25px rgba(1, 1, 1, .07);*/

/*}*/
#user-count[data-v-1e1f2b48], #org-count[data-v-1e1f2b48] {
  color: #c10008;
}
.message-box[data-v-1e1f2b48] {
  margin-top: 22px;
}
.echarts[data-v-1e1f2b48] {
  height: 300px;
}
.ui.image.label[data-v-1e1f2b48] {
  margin-bottom: 6px;
}
#id-message-box[data-v-1e1f2b48] {
  margin-top: 23px;
}


#userprofile-manager[data-v-19dd7364] {
  padding-top: 25px;
}

h3[data-v-033f26fc] {
  text-align: center;
}
#tree[data-v-033f26fc] {
  padding-top: 25px;
}
#tree-panel[data-v-033f26fc] {
  border-right: 2px solid #40adff;
  padding: 0 8px;
}
#pagination[data-v-033f26fc] {
  margin: 12px 0;
}
span.fa.fa-history[data-v-033f26fc] {
  cursor: pointer;
}

#all-orgs[data-v-03174936] {
  padding-top: 25px;
}

#all-users[data-v-325cb2f4] {
  padding-top: 25px;
}

#id-submit-editor[data-v-3a46def8] {
  margin-top: 12px;
}
#tools[data-v-3a46def8], .custom-tree-container[data-v-3a46def8], .userid-input[data-v-3a46def8] {
  padding: 23px;
}
.add-time[data-v-3a46def8] {
  font-size: 12px;
  margin-left: 25px;
}
.msg-content[data-v-3a46def8] {
  margin: 14px;
}
.msg-title[data-v-3a46def8] {
  font-size: 20px;
}
